<template>
  <div>
    <div v-if="fields && fields.length > 0">
      <div class="row no-margin">
        <div class="col s6 m6 l6" v-if="tableTitle">
          <span class="card-title">
            {{tableTitle}}
            <button
              v-if="showColumnFilter"
              class="btn-flat grey lighten-4 red-text notification"
              @click="toggle = !toggle"
              title="View table columns by"
            >
              <span style="vertical-align:top">{{columnFilterName}}</span>
              <i class="material-icons">sort</i>
              <span class="new-count" v-if="showColumnFilterNotification">{{showFieldsList.length}}</span>
            </button>
          </span>
          <div v-show="toggle" id="viewTableCol" class="z-depth-1">
            <label class="uppercase">
              <b>View By</b>
            </label>
            <label v-for="field in fields" :key="field.key">
              <input
                type="checkbox"
                class="filled-in"
                checked
                v-on:change="showHideField(field,$event)"
              />
              <span>{{field.name}}</span>
            </label>
          </div>
        </div>
        <div class="col s4 m1 l1 right-align">
          <button
            v-if="showSearchFilter"
            class="btn-flat grey lighten-4 red-text notification"
            @click="seen = !seen"
            title="Search by"
          >
            <span style="vertical-align:top">{{searchByFilterName}}</span>
            <i class="material-icons">filter_list</i>
            <span
              class="new-count"
              v-if="showSearchFilterNotification"
            >{{defaultSearchColumns.length}}</span>
          </button>
          <div v-show="seen" id="searchTable" class="z-depth-1">
            <label class="uppercase">
              <b>Search By</b>
            </label>
            <label v-for="field in getDisplayFieldsForSearch" :key="field.key">
              <input
                type="checkbox"
                class="filled-in"
                v-on:change="getSearchedData(field,$event)"
                :checked="defaultSearchColumns.indexOf(field.filterkey || field.key) >= 0"
              />
              <span>{{field.name}}</span>
            </label>
          </div>
        </div>
        <div class="col s8 m5 l5 input-field right no-margin" v-if="search === false">
          <i class="material-icons prefix">search</i>
          <input type="text" v-model="searchText" v-on:keyup="searchTextChange()" id="icon_prefix" />
          <label
            v-if="defaultSearchColumns.length > 0"
            for="icon_prefix"
          >Search for {{getSearchableFieldNames().toString()}}</label>
          <label v-else for="icon_prefix">Search...</label>
        </div>
      </div>
      <div class="col s12 tableWrap">
        <table class="striped">
          <thead>
            <tr>
              <th
                v-for="field in getDisplayFields"
                :key="field.key"
                @click="sort(field)"
                :class="field.sortable ? 'pointer':''"
              >
                {{field.name}}
                <i
                  class="material-icons tiny"
                  v-if="getSortingDetails(field)"
                >{{getArrow(field)}}</i>
              </th>
              <th class="uppercase" v-if="actions == 'true'">Actions</th>
            </tr>
          </thead>

          <tbody v-if="entries.length > 0">
            <tr v-for="(entry,r) in entries" :key="r">
              <td v-for="data in getDisplayFields" :key="data.key">
                <div v-if="data.type && data.type == 'html'" v-html="getDisplayValue(entry,data)"></div>
                <div v-else>{{getDisplayValue(entry,data)}}</div>
              </td>
              <td v-if="actions == 'true' && (actionList && actionList.length > 0)">
                <div v-for="(action,a) in actionList" :key="a" class="actionItems pointer">
                  <router-link
                    v-if="action.type == 'link'"
                    :to="action.refAddress(entry)"
                    class="actionIcon"
                  >
                    <div v-if="action.html && action.html == 'true'" v-html="action.getIcon(entry)"></div>
                    <i
                      v-else
                      class="material-icons tiny"
                      :title="action.hoverTitle || ''"
                    >{{action.getIcon(entry)}}</i>
                  </router-link>
                  <div v-else-if="action.type == 'cb'">
                    <div
                      v-if="action.html && action.html == 'true'"
                      v-html="action.getIcon(entry)"
                      @click="action.refAddress(entry)"
                    ></div>
                    <i
                      :title="action.hoverTitle || ''"
                      v-else
                      @click="action.refAddress(entry)"
                      class="material-icons tiny actionIcon"
                    >{{action.getIcon(entry)}}</i>
                  </div>
                </div>
              </td>
            </tr>
          </tbody>
          <tbody v-else>
            <tr>
              <td :colspan="(getFieldLength())" class="center">
                <h6 class="grey-text">No Data Found</h6>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col s12 right-align" id="pageNav">
        <select
          class="browser-default tableRowViewer"
          v-model="tableData.recordsPerPage"
          v-on:change="
          resetPerPage()"
        >
          <option
            v-for="(option) in getPerPageOptions()"
            :value="option"
            :key="option"
          >Show {{option}}</option>
        </select>
        <span>
          <b>Showing {{tableData.totalRecords == 0 ? 0 : getTable.start+1}} to {{getTable.end}} of {{getTable.totalRecords}}</b>
        </span>
        <button
          class="btn-floating btn-small white btn-flat"
          @click="changePage('p')"
          :disabled="tableData.start <= 0"
        >
          <i
            class="material-icons"
            :class="tableData.start <= 0 ? 'grey-text' : 'red-text'"
          >navigate_before</i>
        </button>
        <button
          class="btn-floating btn-small white btn-flat"
          @click="changePage('f')"
          :disabled="tableData.end >= tableData.totalRecords"
        >
          <i
            class="material-icons"
            :class="tableData.end >= tableData.totalRecords ? 'grey-text' : 'red-text'"
          >navigate_next</i>
        </button>
      </div>
    </div>
    <div v-else>No Data Available</div>
  </div>
</template>

<script>
export default {
  computed: {
    getTable() {
      return this.tableData;
    },
    getCurrentSort() {
      return this.defaultSort;
    },
    getCurrentSortDir() {
      return this.defaultSortDir;
    },
    getDisplayFields() {
      let displayFields = this.fields.filter(
        data => this.showFieldsList.indexOf(data.name) >= 0
      );
      return displayFields;
    },
    getDisplayFieldsForSearch() {
      let displayFields = this.fields.filter(
        data =>
          this.showFieldsList.indexOf(data.name) >= 0 &&
          data.filterable != false
      );
      return displayFields;
    }
  },
  mounted() {
    this.currentSort =
      this.currentSort == "" ? this.defaultSort : this.currentSort;
    this.currentSortDir =
      this.currentSortDir == "" ? this.defaultSortDir : this.currentSortDir;
    this.tableData.recordsPerPage = this.getPerPageOptions()[0];

    this.fields.map(data => {
      this.showFieldsList.push(data.name);
    });
    this.loadEvents();
  },
  props: {
    fields: Array,
    perPageOptions: Array,
    actionList: Array,
    loadEntries: Function,
    defaultSort: String,
    defaultSearchColumns: Array,
    defaultSortDir: String,
    tableTitle: String,
    actions: String,
    search: Boolean,
    showColumnFilter: Boolean,
    columnFilterName: String,
    showColumnFilterNotification: Boolean,
    showSearchFilter: Boolean,
    searchByFilterName: String,
    showSearchFilterNotification: Boolean
  },
  methods: {
    getSearchableFieldNames() {
      let allNames = [];
      this.getDisplayFields.map(data => {
        if (
          this.defaultSearchColumns.indexOf(data.filterkey || data.key) >= 0
        ) {
          allNames.push(data.name);
        }
      });
      return allNames;
    },
    getSearchedData(_field, event) {
      if (event.target.checked) {
        this.defaultSearchColumns.push(_field.filterkey || _field.key);
        this.searchText ? this.searchTextChange() : "";
      } else {
        let index = this.defaultSearchColumns.indexOf(
          _field.filterkey || _field.key
        );
        this.defaultSearchColumns.splice(index, 1);
        this.searchText ? this.searchTextChange() : "";
      }
    },
    showHideField(_field, event) {
      console.log(_field.name);
      if (event.target.checked) {
        this.showFieldsList.push(_field.name);
        if (_field.filterable != false) {
          this.defaultSearchColumns.push(_field.filterkey || _field.key);
          this.searchText ? this.searchTextChange() : "";
        }
      } else {
        let index = this.showFieldsList.indexOf(_field.name);
        let index2 = this.defaultSearchColumns.indexOf(
          _field.filterkey || _field.key
        );
        console.log("index2====", index2);
        if (index2 >= 0) {
          this.defaultSearchColumns.splice(index2, 1);
          this.searchText ? this.searchTextChange() : "";
        }
        this.showFieldsList.splice(index, 1);
      }
    },
    getPerPageOptions() {
      if (this.perPageOptions && this.perPageOptions.length > 0) {
        return this.perPageOptions;
      } else {
        return [3, 5, 7, 10, 15, 20];
      }
    },
    getDisplayValue(entry, data) {
      let key = data.key;
      let value = data.defaultValue || "-";
      try {
        value = eval(`entry.${key}`);
        if (data.preProcess) {
          return data.preProcess(value, entry);
        } else {
          return value;
        }
      } catch (e) {
        return value;
      }
    },
    getFieldLength() {
      let extraColumn = 1;
      return this.actions == "true"
        ? this.fields.length + extraColumn
        : this.fields.length;
    },
    changePage(direction) {
      if (direction == "f") {
        this.tableData.start += this.getTable.recordsPerPage;
        this.loadEvents();
      } else {
        this.tableData.start -= this.getTable.recordsPerPage;
        this.loadEvents();
      }
    },
    updateTableParams(result) {
      this.tableData.totalRecords = result.count;
      this.tableData.end = this.getTable.start + this.getTable.recordsPerPage;
      /*  this.currentSort =
        this.currentSort == "" ? this.defaultSort : this.currentSort;
      this.currentSortDir =
        this.currentSortDir == "" ? this.defaultSortDir : this.currentSortDir; */
      if (this.tableData.end > this.tableData.totalRecords) {
        if (this.tableData.totalRecords >= 0) {
          this.tableData.end = this.tableData.totalRecords;
        } else {
          this.tableData.end = 0;
          this.tableData.totalRecords = 0;
        }
      }
    },
    resetPerPage() {
      this.tableData.start = 0;
      this.loadEvents();
    },
    loadEvents(hideLoading = false) {
      this.loadEntries(
        this.tableData.start,
        this.tableData.recordsPerPage,
        this.currentSort,
        this.currentSortDir,
        this.searchText,
        this.defaultSearchColumns,
        hideLoading,
        result => {
          this.updateTableParams(result);
          if (result.status == 200) {
            this.entries = result.msg;
          } else {
            alert(result.msg); //window.location = "/";
            //window.location = "/";
          }
        }
      );
    },
    getSortingDetails(field) {
      return field.sortable == true;
    },
    getArrow(field) {
      if (
        this.currentSort &&
        this.currentSort == (field.sortkey || field.key)
      ) {
        return this.currentSortDir == "asc" ? "arrow_upward" : "arrow_downward";
      } else {
        return "sort";
      }
    },
    sort(field) {
      if (field.sortable) {
        let s = field.sortkey || field.key;
        if (s === this.currentSort) {
          this.currentSortDir = this.currentSortDir === "asc" ? "desc" : "asc";
          //this.defaultSortDir = this.currentSortDir === "asc" ? "desc" : "asc";
        }
        this.currentSort = s;
        //this.defaultSort = s;
        this.tableData.start = 0;
        this.loadEvents();
      }
    },
    searchTextChange() {
      if (this.timeOut) clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => {
        this.tableData.start = 0;
        this.loadEvents(true);
      }, 200);
    }
  },
  data() {
    return {
      tableData: {
        start: 0,
        end: 0,
        recordsPerPage: 3, //PROJECT_PER_PAGE
        totalRecords: 0 //PROJECT_PER_PAGE
      },
      currentSort: "",
      currentSortDir: "",
      entries: [],
      searchText: "",
      timeOut: null,
      toggle: false,
      seen: false,
      showFieldsList: [],
      hideFieldsList: []
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pointer {
  cursor: pointer;
}
.actionItems {
  display: inline-block;
  color: #2196f3;
  margin-right: 5px;
}
.actionIcon {
  color: #2196f3;
}
#viewTableCol,
#searchTable {
  width: 300px;
  display: block;
  background: #fff;
  padding: 12px 14px;
  position: absolute;
  z-index: 99;
  text-align: left;
}
#viewTableCol label,
#searchTable label {
  display: block;
  margin-bottom: 4px;
}
.notification {
  position: relative;
  display: inline-block;
  border-radius: 2px;
}
.notification .new-count {
  position: absolute;
  top: -10px;
  right: -10px;
  padding: 5px 10px;
  border-radius: 50%;
  background: red;
  color: white;
  width: 24px;
  height: 24px;
  font-size: 12px;
  line-height: 18px;
  padding: 3px;
}
</style>
